.App {
  text-align: center;
}

@media only screen and (max-width: 700px) {
  :root {
    --columns: 1;
  }
}
@media only screen and (min-width: 700px) {
  :root {
    --columns: 2;
  }
}

.wrapper {
  display: grid;
  grid-column: auto;
  grid-gap: var(--gutter-width);
  grid-template-columns: repeat(var(--columns), 1fr);
}

.product {
  padding: 10px 5px;
  margin: 2px 5px;
  border-radius: 10px;
  border: 2px solid #25b8b1f6;
  display: grid;
  background-color: var(--primary);
  grid-gap: var(--gutter-width);
  grid-template-columns: 65% 35%;
}

.product .name {
  font-family: "Jura", sans-serif;
  font-size: 16px;
  font-weight: 500;
}

.product .name_ar {
  font-family: "Cairo", sans-serif;
  font-size: 16px;
  font-weight: 700;
}

.product .price {
  font-family: "Jura", sans-serif;
  background-color: var(--secondery);
  border-radius: 10px;
  padding: 2px;
  display: grid;
  grid-template-columns: 35% 10%;
  align-items: center;
  width: calc(55px + 25%);
  max-width: 140px;
  height: 35px;
  color: white;
  font-size: 14px;
  font-weight: 600;
}

.currencyTag {
  font-size: 10px;
}

.product .pv {
  box-shadow: inset 0px 0px 0px 1px var(--secondery);
  font-family: "Jura", sans-serif;
  border-radius: 10px;
  width: calc(55px + 25%);
  padding: 2px;
  max-width: 140px;
  align-items: center;
  display: grid;
  height: 25px;
  font-size: 14px;
  font-weight: 500;
}

.dollarVal {
  transition: 0.3s;
  color: white;
  text-align: center;
  font-family: "Jura", sans-serif;
  font-size: large;
  font-weight: 700;
  background-color: var(--secondery);
  width: 100px;
  left: -80px;
  top: 250px;
  padding: 5px 25px 10px 10px;
  border-radius: 0px 10px 10px 0px;
  position: fixed;
}

.dollarVal:hover {
  transition: 0.3s;
  left: 0px;
}
